/*  =============== Google Fonts =============== */
@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700;800;900&display=swap');

/*  =============== Variables css =============== */
:root {
  /* ----- Colors ----- */
  --first-color: hsl(353, 100%, 65%);
  --second-color: hsl(43, 100%, 50%);
  --third-color: hsl(240, 100%, 71%);
  --title-color: hsl(244, 24%, 26%);
  --text-color: hsl(244, 16%, 43%);
  --body-color: hsl(258, 60%, 98%);
  --container-color: #fff;
  --light-gary : #eee;
  --light2-gary : #cecece;

  /* ----- Font Family Typographic ----- */
  --body-font: 'Rubik', sans-serif;
  /* ----- Font Size ----- */
  --h1-font-size: 2.25rem;
  --h2-font-size: 1.5rem;
  --h3-font-size: 1.25rem;
  --normal-font-size: 1rem;
  --small-font-size: 0.875rem;
  --smaller-font-size: 0.813rem;
  /* ----- Font weight ----- */
  --font-medium: 500;
  --font-semibold: 600;
  --font-bold: 700;


  /* ----- Box Shadow ----- */
  --shadow: 0 5px 20px rgb(69 67 96 / 10%);
  --shadow-first: 0 5px 20px rgb(255 77 97 / 50%);
  --shadow-second: 0 5px 20px rgb(255 183 0 / 50%);
  --shadow-third: 0 5px 20px rgb(108 108 255 / 50%);


  /* ----- Border Radius ----- */
  --border-radius: 20px
}

/*  =============== Responsive Typographic css ============== */
@media screen and (max-width : 1024px) {
  :root{
    --h1-font-size: 1.75rem;
    --h2-font-size: 1.25rem;
    --h3-font-size: 1rem;
    --normal-font-size: 0.938rem;
    --small-font-size: 0.813rem;
    --smaller-font-size: 0.75rem;
  }
}

/*  =============== base css =============== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body,
button,
input,
textarea {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
}

body {
  background-color: var(--body-color);
  color: var(--text-color);
  line-height: 1.7;
}

h1,
h2,
h3 {
  color: var(--title-color);
  font-weight: var(--font-semibold);
  line-height: 1.2;
}

ul {
  list-style: none;
}

a {
  display: inline-block;
  text-decoration: none;
  color: inherit;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

/*  =============== Reuseable  css =============== */

.container {
  max-width: 1080px;
  margin-inline: auto;
  padding-inline: 15px;
}

.section {
  padding-top: 7rem;
  padding-bottom: 2rem;
}

.section__title {
  font-size: var(--h1-font-size);
  font-weight: var(--font-bold);
  margin-left: 0.875rem;
  margin-bottom: 3.75rem;
  position: relative;
}

.section__title::before {
  content: "";
  background-image: url(./assets/dots-bg.svg);
  height: 2.25rem;
  width: 2.25rem;
  position: absolute;
  left: -0.875rem;
  top: -0.875rem;

}

.grid {
  display: grid;
}

.flex {
  display: flex;
}

.btn{
  padding: 0.75rem 2rem;
  line-height: 1;
  border-radius: 1.875rem;
  border: solid 1px transparent;
  box-shadow: 0 0 1px rgb(0 0 0 / 0%);
  color: var(--container-color);
  background-color: var(--first-color);
  font-weight: var(--font-bold);
}
.btn:hover {
  animation: button-push 0.3s linear;
}

@keyframes button-push {
  50%{
    transform: scale(0.8);
  }
  100%{
    transform: scale(1);

  }
}