.home {
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.intro {
  max-width: 540px;
  text-align: center;
}

.home__img {
  margin-bottom: 1rem;
  width: 170px;
}

.home__name {
  font-size: var(--h1-font-size);
  font-weight: var(--font-bold);
  margin-bottom: 0.2rem;
}

.home__socials {
  display: flex;
  gap: 1rem;
  justify-content: center;
  margin: 1rem 0 1rem;
}

.home__social-link {
  font-size: var(--h2-font-size);
  transition: 0.3s;
  color: var(--title-color);
}

.home__social-link:hover {
  color: var(--first-color);
  animation: button-push 0.3s linear;
}

.scroll__down {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
}

.home__scroll-name {
  font-size: var(--small-font-size);
}

.mouse {
  border: 2px solid var(--text-color);
  border-radius: 1rem;
  display: block;
  width: 1.25rem;
  height: 2rem;
  margin: auto;
  position: relative;
}

.wheel {
  animation: moveDown 1s ease-in-out infinite both alternate;
  width: 0.3rem;
  height: 0.3rem;
  background-color: var(--title-color);
  border-radius: 50%;
  display: block;
  position: absolute;
  top: 0.2rem;
  left: 50%;
  transform: translateX(-50%);
}

@keyframes moveDown {
  100% {
    top: calc(100% - 0.5rem);
  }
}


.shapes {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
}

.shape {
  position: absolute;
  animation: button-push 3s ease-in-out infinite alternate;


}

.s1 {
  left: 2%;
  top: 10%;
}

.s2 {
  left: 18%;
  top: 30%;
}

.s3 {
  left: 5%;
  bottom: 30%;
}

.s4 {
  left: 2%;
  bottom: 10%;
}

.s5 {
  left: 44%;
  top: 10%;
}

.s6 {
  left: 36%;
  bottom: 10%;
}

.s7 {
  top: 20%;
  right: 25%;
}

.s8 {
  right: 24%;
  bottom: 20%;
}

.s9 {
  right: 2%;
  top: 10%;
}

.s10 {
  top: 45%;
  right: 11%;
}

.s11 {
  bottom: 10%;
  right: 2%;
}


@media screen and (max-width: 1024px) {
  .home__social-link {
    font-size: 1rem
  }

}