.services__container {
  grid-template-columns: repeat(3,1fr);
  gap: 1.875rem;
}

.services__card {
  text-align: center;
  padding: 1.875rem;
  border-radius: var(--border-radius);
  background-color: var(--container-color);
  transition: 0.3s ease-in-out;
  color: var(--container-color);

}

.services__card:hover {
  transform: translateY(-10px);
}

.services__card:nth-child(1){
  background-color: var(--third-color);
  box-shadow: var(--shadow-third)
}
.services__card:nth-child(2){
  background-color: var(--second-color);
  box-shadow: var(--shadow-second)
}
.services__card:nth-child(3){
  background-color: var(--first-color);
  box-shadow: var(--shadow-first);
}

.services__img {
  margin-bottom: 1.25rem;
  filter:  drop-shadow(0 0 2px var(--light-gary));
}
.services__title {
  color: var(--container-color);
  font-size: var(--h3-font-size);
  margin-bottom: 1rem;
}
.services__description {
  color: var(--light-gary);
}

.services__card:nth-child(2) .services__title{
  color: var(--title-color);
}
.services__card:nth-child(2) .services__description{
  color: var(--text-color);
}


@media screen and (max-width: 1024px) {
  .services__container {
    grid-template-columns: repeat(2,1fr);
    justify-content: center;
  }
}

@media screen and (max-width: 768px){
  .services__container {
    grid-template-columns: repeat(1,1fr);
    justify-content: center;

  }
}