.about__container {
  grid-template-columns: 3fr 9fr;
  gap: 1.875rem;
}

.about__data {
  background-color: var(--container-color);
  box-shadow: var(--shadow);
  padding: 1.875rem;
  border-radius: var(--border-radius);

  grid-template-columns: repeat(2, 1fr);
  gap: 1.875rem;
  align-items: start;
  position: relative;
}

.about__data::before {
  content: "";
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-right: 15px solid var(--container-color);
  position: absolute;
  right: 100%;
  top: 20%;

}


.about__description {
  margin-bottom: 1rem;
  text-align: justify;
  letter-spacing: 1px;
}

.about__skills {
  row-gap: 1.25rem;
}

.skills__titles {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.skills__name {
  font-size: var(--normal-font-size);
  font-weight: var(--font-medium);
}

.skills__number {
  line-height: 1.2;
}

.skills__bar,
.skills__percentage {
  height: 7px;
  border-radius: var(--border-radius);
}

.skills__bar {
  background-color: var(--light-gary);
}

.skills__percentage {
  display: block;
  width: 0;
}

.development {
  width: 90%;
  background-color: var(--second-color);

}

.ui__ux {
  width: 80%;
  background-color: var(--first-color);

}

.photography {
  width: 60%;
  background-color: var(--third-color);
}


.about__boxes {
  margin-top: 4.25rem;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.875rem;
}

.about__box {
  display: flex;
  gap: 1.5rem;
}

.about__icon {
  font-size: var(--h1-font-size);
  color: var(--light2-gary);
}

.about__title {
  font-size: 1.875rem;
}

@media screen and (max-width: 1024px) {

  .about__container {
    grid-template-columns: 1fr;
    justify-content: center;
  }

  .about__data::before{ 
    border: 10px solid transparent;
    border-bottom: 10px solid var(--container-color);
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50% , -100%);
  }
  .about__img {
    justify-self: center;
  }
}

@media screen and (max-width: 768px){
.about__data  {
  grid-template-columns: repeat(1 ,1fr);
}
.about__boxes {
  margin-top: 4.25rem;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.875rem;
}
}